





























































#logo {
  @apply bg-no-repeat bg-center overflow-hidden cursor-pointer transition-default;
  text-indent: -999999999px;
  width: 222px;
  min-width: 222px;
  height: 74px;
  background-image: url('~@assets/logo.png');
  background-size: contain;
}

.navigation {
  @apply flex items-center justify-between p-3;

  a {
    @apply text-base;
  }
}
